/* Base Styles */
.tree-view-container {
  display: flex;
  margin-left: 50px;
  width: 80%;
  height: 100vh;
}

.menu-list-container {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 450px;
  background-color: rgb(23, 92, 240);
  color: white; /* Make text more readable */
  border-radius: 8px; /* Rounded corners for better look */
  overflow-y: auto; /* Enable vertical scrolling if content overflows */
}

.menu-list-container li {
  margin: 0;
  padding: 10px 15px; /* Increase padding for better touch target size */
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); /* Add separator between items */
}

.menu-list-container li:hover {
  background-color: rgba(255, 255, 255, 0.1); /* Highlight item on hover */
}

.tree-content {
  flex: 1;
  padding: 20px;
  background-color: rgb(48, 166, 187);
  border-left: 5px solid #5a5353;
  border-radius: 8px; /* Rounded corners for better look */
  overflow-y: auto; /* Enable vertical scrolling if content overflows */
  color: white; /* Make text more readable */
}

/* Mobile Responsive Styles */
@media (max-width: 768px) {
  .tree-view-container {
    flex-direction: column; /* Stack elements vertically */
    margin-left: 20px;
    width: 90%;
  }

  .menu-list-container {
    width: 100%;
    margin-bottom: 20px; /* Add space between menu and content */
  }

  .tree-content {
    border-left: none;
    border-top: 5px solid #5a5353; /* Change border to top for better separation */
  }
}

@media (max-width: 480px) {
  .tree-view-container {
    margin-left: 10px;
    width: 100%;
  }

  .menu-list-container {
    width: 100%;
  }

  .tree-content {
    padding: 10px;
  }
}
